<template>
  <ik-data-table
    ref="table"
    :model="model"
    :entity-name="$t('分类')"
    icon="mdi-label"
    style="width: 100%"
    @reloaded="reloaded"
  >
    <template v-slot:footer>
      <v-btn
        class="primary ml-4"
        @click="setRandomColor"
      >
        {{ $t('随机设置标签颜色') }}
      </v-btn>

      <v-btn
        class="red"
        @click="removeColor"
      >
        {{ $t('消除所有标签颜色') }}
      </v-btn>
    </template>
  </ik-data-table>
</template>

<script>
  import { IkDataTable } from 'metaflow-js'
  import Category from '../../model/Category'

  export default {
    components: {
      IkDataTable,
    },
    data: function () {
      return ({
        model: Category,
        response: null,
      })
    },

    async mounted () {

    },
    methods: {
      reloaded () {
        // this.$refs.table.reload()
        // console.log('reloaded')
      },
      async setRandomColor () {
        console.log(this.response, 'this.responese set')
        this.model.randomColor(await this.model.getList())
      },
      async removeColor () {
        console.log(this.response, 'this.responese remove')
        this.model.removeAllColor(await this.model.getList())
      },
    },
  }
</script>
